@tailwind base;
@tailwind components;
@tailwind utilities;


.slide-in {
  animation: slide 0.4s linear 1; /* animation-iteration-count를 1로 설정하여 한 번만 실행 */
}

@keyframes slide {
  0% {
    transform: translateX(100%); /* 오른쪽에서 시작 */
  }
  100% {
    transform: translateX(0); /* 왼쪽으로 이동 */
  }
}

/* History.css */

.slide-in-bg {
  animation: slideIn 1s ease-in-out 1;
  animation-fill-mode: forwards;
  transform: translateY(100%);
}

@keyframes slideIn {
  to {
    transform: translateY(0);
  }
}
.image-container {
  transition: transform 1.0s; /* 변환을 부드럽게 만들기 위한 트랜지션 */
}

.image-container.large {
  transform: scale(1.5); /* 스크롤을 내릴 때 이미지 크기를 확대 */
}

.image-container.small {
  transform: scale(0.7); /* 스크롤을 올릴 때 이미지 크기를 축소 */
}
.backgroundImageDiv {
  width: 100%;
  height: auto; /* 화면 높이의 100%로 설정 */
  /* background-image: url('.././public/image/marketing07.jpg'); */

  /* background-repeat: repeat; */
} 

.hover-container {
  position: relative;
}

.hidden-content {
  display: none;
  position: absolute;
  top: 0;
  left: 0%;
  width: 100%;
  height: 100%;
  background-color: transparent; /* If you want it to be transparent */
}

.hover-container:hover .hidden-content {
  display: block;
}